@font-face {
  font-family: 'Suit Variable';
  src: url('../public/fonts/SUIT-Variable.ttf');
}

*, ::before, ::after {
  box-sizing: border-box;
}
::before, ::after {
  text-decoration: inherit;
  vertical-align: inherit;
}
.cf:after {
  display: block;
  content: "";
  clear: both;
}
html {
  width: 100%;
  height: 100%;
  cursor: default;
  -moz-tab-size: 4;
  tab-size: 4;
  -webkit-tap-highlight-color: transparent;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
  word-break: break-word;
}
body, button, dd, dl, dt, fieldset, form, h1, h2, h3, h4, h5, h6, input, legend, li, ol, p, select, table, td, textarea, th, ul {
  margin: 0;
  padding: 0;
  margin-block-start: 0;
  margin-block-end: 0;
  margin-inline-start: 0;
  margin-inline-end: 0;
}
body, button, input, select, table, textarea {
  font-size: 16px;
  line-height: 1.5;
  color: #101010;
  font-family: "SUIT Variable", "Apple SD Gothic Neo", arial, dotum, 돋움, sans-serif;
}
h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  line-height: inherit;
}
hr {
  height: 0;
  overflow: visible;
}
main, details {
  display: block;
}
dl, ol, ul, menu {
  list-style: none;
}
pre, code, kbd, samp {
  font-family: monospace, monospace;
  font-size: 1em;
}
address, em, i {
  font-style: normal;
}
a {
  background-color: transparent;
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}
a:hover {
  text-decoration: none;
}
abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
}
b, strong {
  font-weight: bolder;
}
small {
  font-size: 80%;
}
sub, sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}
sub {
  bottom: -0.25em;
}
sup {
  top: -0.5em;
}
audio, canvas, iframe, img, svg, video {
  vertical-align: middle;
}
audio, video {
  display: inline-block;
}
img {
  vertical-align: top;
}
iframe, img, fieldset {
  border-style: none;
}
img, video {
  max-width: 100%;
}
svg:not([fill]) {
  fill: currentColor;
}
svg:not(:root) {
  overflow: hidden;
}
img[src$=".gif"], img[src$=".png"] {
  image-rendering: -moz-crisp-edges;
  image-rendering: -o-crisp-edges;
  image-rendering: -webkit-optimize-contrast;
  image-rendering: crisp-edges;
  -ms-interpolation-mode: nearest-neighbor;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
button {
  background-color: transparent;
}
button, select {
  text-transform: none;
}
button, input {
  border: 0;
  border-radius: 0;
  overflow: visible;
}
input:focus {
  outline: none;
}
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  transition: background-color 5000s ease-in-out 0s;
  -webkit-transition: background-color 9999s ease-out;
  -webkit-box-shadow: 0 0 0px 1000px var(--white) inset !important;
  -webkit-text-fill-color: var(--black) !important;
}
input:autofill,
input:autofill:hover,
input:autofill:focus,
input:autofill:active {
  transition: background-color 5000s ease-in-out 0s;
  -webkit-transition: background-color 9999s ease-out;
  -webkit-box-shadow: 0 0 0px 1000px var(--white) inset !important;
  -webkit-text-fill-color: var(--black) !important;
}
input:-internal-edge-excel-like-autofill-previewed {
  color: var(--black) !important;
}
button, [type="button"], [type="reset"], [type="submit"] {
  -webkit-appearance: button;
}
figure {
  margin: 0;
}
fieldset {
  border: 1px solid #a0a0a0; padding: 0.35em 0.75em 0.625em;
}
input {
  overflow: visible;
}
legend {
  color: inherit;
  display: table;
  max-width: 100%;
  white-space: normal;
}
progress {
  display: inline-block;
  vertical-align: baseline;
}
select {
  text-transform: none;
}
textarea {
  overflow: auto;
  resize: vertical;
}
[type="checkbox"], [type="radio"] {
  padding: 0;
}
[type="search"] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}
::-webkit-inner-spin-button, ::-webkit-outer-spin-button {
  height: auto;
}
::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54;
}
::-webkit-search-decoration {
  -webkit-appearance: none;
}
::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}
::-moz-focus-inner {
  border-style: none; padding: 0;
}
:-moz-focusring {
  outline: 1px dotted ButtonText;
}
:-moz-ui-invalid {
  box-shadow: none;
}
dialog {
  background-color: #fff;
  border: solid;
  color: #000;
  display: block;
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
  left: 0;
  margin: auto;
  padding: 1em;
  position: absolute;
  right: 0;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
}
dialog:not([open]) {
  display: none;
}
summary {
  display: list-item;
}
a, area, button, input, label, select, summary, textarea, [tabindex] {
  -ms-touch-action: manipulation;
  touch-action: manipulation;
}
[hidden] {
  display: none;
}
[aria-busy="true"] {
  cursor: progress;
}
[aria-controls] {
  cursor: pointer;
}
[aria-disabled="true"], [disabled] {
  cursor: not-allowed;
}
[aria-hidden="false"][hidden] {
  display: initial;
}
[aria-hidden="false"][hidden]:not(:focus) {
  clip: rect(0, 0, 0, 0); position: absolute;
}

body {
  --black: #121619;
  --yellow: #FFDB58;
  --pink: #EFD5C0;
  --grey: #E4E2DA;
  --white: #F9F9F9;

  margin: 0;
  background-color: var(--black);
  color: var(--white);
}
body:has(.page.dimmed) {
  overflow: hidden;
}